  opacity: 0.5;
  cursor: wait;
}

.content {
  color: black;
  background-color: white;
  text-align: center;
}

input[type="file"] {
    display: none;
}

.file-upload-label {
    border: 2px solid #ccc;
    display: flex;
    padding: 0.25rem 0.5rem;
